@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import "~@itv/fe-components/cjs/common/styles/tokens/variables";
@import "~@itv/fe-components/cjs/common/styles/library/typography";

.DeleteProfileModal {
    .OverlayModal__content {
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: $spacing-09;
        min-width: 495px;
        box-shadow: 0 3px 6px 4.5px rgb(0 0 0 / 50%);
        border-radius: $radius-03;
    }

    &__heading {
        margin-bottom: $spacing-07;
        @include itv-font-type(title-1);
    }

    &__prompt {
        margin-bottom: $spacing-07;
        display: block;
        @include itv-font-type(body-large);
    }

    &__cancel-button {
        margin-right: $spacing-05;
    }
}
