@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';
@import '~@itv/fe-components/cjs/common/styles/library/typography';

.ProfileAvatar__button {
    width: 100%;
    padding: 0;
}

.header {
    $self: &;
    width: 100%;
    background: $gradient-itvx-linear-secondary;

    &__elements {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: inherit;
        padding: $spacing-10 144px;
    }

    &__avatarContainer {
        width: 753px;

        .Button:hover, .Button {
            border: none;
            background: none;
        }

        .Button.focus, .Button:hover{
            background-color: transparent;
        }
    }

    &__rule {
        margin: 0;
        border-image-source: linear-gradient(270deg, rgba(200, 200, 200, 0.0001) 0.43%, #979797 54.22%, rgba(151, 151, 151, 0.0112092) 99.57%);
    }
}
