@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';
@import '../../../styles/variables';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';

.EditProfile {
    width: $app-width;
    height: $app-height;
    display: flex;
    flex-direction: column;
    background: $background-linear-gradient;

    &__done-button {
        width: 120px;
        height: 66px;
        min-width: unset;

        &--child {
            margin-top: $spacing-12;
            margin-left: 144px;
        }

        &.focus {
            background: $color-itvx-background-inverse;
            color: $color-itvx-text-inverse;
            font-weight: 400;
        }
    }

    .cp_icon--small {
        max-width: 49px;
        max-height: 49px;
    }

    .cp_button {
        text-transform: none;
    }

    .cp_avatar {
        .cp_icon {
            position: absolute;
        }
    }
}
