@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';
@import '~@itv/fe-components/cjs/common/styles/library/typography';

.parental-controls {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 144px;
    height: 300px;
    margin-top: 96px;

    &__settingsWrapper {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-self: baseline;
        max-width: 753px;
    }

    &__headerContainer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: $spacing-08;
    }

    &__info-container {
        max-width: 792px;
        margin-left: 87px;
    }

    &__info {
        background-color: $color-itvx-background-tertiary;
        padding: $spacing-08-5;
        border-radius: $radius-03;

        &-heading {
            @include itv-font-type(title-2, bold);
            margin-bottom: $spacing-08-5;
        }

        &-statement {
            @include itv-font-type(body-large);
        }
    }
}
